.credit-container {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: url("../img/night2.jpg");
  background-size: cover;
  overflow: hidden;
  padding-top: 50px;
}

#credits-avatar {
  max-width: 200px;
  height: 200px;
}

.credit-container h1 {
  text-transform: uppercase;
  text-align: center;
  color: white;
  font-size: 40px;
  margin: auto;
}

.credit-container h2 {
  text-align: center;
}

.credit-sep {
  display: block;
  margin: auto;
  width: 160px;
  height: 3px;
  background: #2ecc71;
  margin-bottom: 40px;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.credit-devs {
  margin-bottom: 40px;
  opacity: 0;
  -webkit-animation: fadeIn ease-in 1;
          animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.credit-devs a {
  display: inline-block;
  margin: 0 30px;
  width: 160px;
  height: 160px;
  overflow: hidden;
  border-radius: 50%;
}

.credit-devs a img {
  widows: 100%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  -webkit-transition: 0.4s all;
  -o-transition: 0.4s all;
  transition: 0.4s all;
}

.credit-devs a:hover > img {
  -webkit-filter: none;
          filter: none;
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

.credits-desc {
  width: 600px;
  margin: auto;
  font-size: 20px;
  color: white;
  text-align: justify;
  height: 0;
  overflow: hidden;
}

.credits-desc:target {
  height: auto;
  opacity: 0;
  -webkit-animation: fadeIn ease-in 1;
          animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.credits-name {
  display: block;
  margin-bottom: 30px;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
}

#credits-head {
  color: white;
  width: 22.5%;
  margin: auto;
  font-size: 43px;
}
#credits-watch {
  color: #00c853;
}
#credits-aboutus {
  color: #f39c12;
  font-size: 20px;
}



